import React from "react";
import Button from "../common/GradientButton";
import { useAuth } from "../../context/AuthContext";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShoppingCart, faHeart } from "@fortawesome/free-solid-svg-icons";
import Swal from 'sweetalert2'

const ProductCardClient = (props) => {
  const { authState, setProductsCart, setProductsLike, isAuthenticated } =
    useAuth();

  const handleClick = () => {
    let pd = props.product;
    if (
      authState.productsCart &&
      authState.productsCart instanceof Array &&
      authState.productsCart.length
    ) {
      console.log(authState.productsCart);
      if (pd) {
        if (!authState.productsCart.find((p) => p._id === pd._id)) {
          setProductsCart([...authState.productsCart, pd]);
          Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'added to shopping cart',
            showConfirmButton: false,
            timer: 1500
          })
        }
      }
    } else if (pd) {
      setProductsCart([pd]);
      Swal.fire({
        position: 'top-center',
        icon: 'success',
        title: 'added to shopping cart',
        showConfirmButton: false,
        timer: 1500
      })
    }
  };

  const handleClickLike = () => {
    const pd = props.product;
    if (
      authState.productsLike &&
      authState.productsLike instanceof Array &&
      authState.productsLike.length
    ) {
      if (pd) {
        if (!authState.productsLike.find((p) => p._id === pd._id)) {
          setProductsLike([...authState.productsLike, pd]);
          Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'added to favorites',
            showConfirmButton: false,
            timer: 1500
          })
        }
      }
    } else if (pd) {
      setProductsLike([pd]);
      Swal.fire({
        position: 'top-center',
        icon: 'success',
        title: 'added to favorites',
        showConfirmButton: false,
        timer: 1500
      })
    }
  };

  return (
    <>
      <div className="w-64 h-auto relative">
        <div className="pb-3 bg-white py-4 px-3 border-4 rounded border-gray-200 h-full shadow-md">
          <div className="flex justify-center item-center text-center">
            <img src={props.product.image} className="w-56 h-48 " alt="" />
          </div>
          <div className="flex justify-center text-center flex-col">
            <div className="py-4 flex justify-center space-x-2 items-center text-center">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 text-green-500"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path d="M5 4a2 2 0 012-2h6a2 2 0 012 2v14l-5-2.5L5 18V4z" />
              </svg>
              <p className="text-lg font-semibold text-center font-nucleo">
                {props.product.name}
              </p>
            </div>
            <div className="py-1">
              <p className="text-sm font-semibold font-serif">Price:</p>
              <span className="relative inline-block px-3 py-1 font-semibold text-green-900 leading-tight">
                <span
                  aria-hidden="true"
                  className="absolute inset-0 bg-green-200 opacity-50 rounded-full"
                ></span>
                <span className="relative">
                  <p className="text-md font-semibold font-nucleo">
                    ${props.product.price}
                  </p>
                </span>
              </span>
            </div>
          </div>
          <div className="bottom-0">
            <div className="flex justify-center text-center">
              <div className="bg-santex py-4 px-6 w-full ">
                <p className="text-white text-md font-semibold pb-2 font-sans">
                  {props.product.description}
                </p>
              </div>
            </div>
            {!isAuthenticated() ? (
              <>
                <div className="bg-gradient shadow-lg py-3">
                  <div className="flex flex-col justify-center text-center items-center pl-4">
                    <Button to="/login" text="Know more" />
                    <FontAwesomeIcon icon={faHeart} />
                  </div>
                </div>
              </>
            ) : (
              <>
                <div className="justify-center bg-gradient shadow-lg text-red text-center grid grid-cols-2 gap-2 py-3 pb-3">
                  <div className="flex flex-col justify-center text-center items-center pl-4">
                    <button
                      className="flex rounded-full items-center py-2 px-6 bg-gradient focus:outline-none shadow-lg text-white"
                      onClick={handleClickLike}
                    >
                      <FontAwesomeIcon icon={faHeart} />
                    </button>
                  </div>
                  <div className="flex flex-col justify-center text-center items-center">
                    <button
                      className="flex rounded-full items-center py-2 px-6 bg-gradient focus:outline-none shadow-lg text-white"
                      onClick={handleClick}
                    >
                      <FontAwesomeIcon icon={faShoppingCart} />
                    </button>
                  </div>
                </div>
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductCardClient;
